* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: rgba(26, 32, 44);
    --color-bg-variant: rgba(45, 55, 72);
    --color-primary: rgba(129, 230, 217);
    --color-primary-variant: rgba(79, 209, 197);
    --color-white: #fff;
    --color-light: rgba(160, 174, 192);

    --transition: all 400rem ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: "Poppins", sans-serif;
    background: rgba(26, 32, 44);
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);

}

/*  =========== GENERAL STYLES ============== */
.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: rgba(160, 174, 192);
    color: var(--color-light);
}

section > h2 {
    color: rgba(129, 230, 217);
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(160, 174, 192);
    color: var(--color-light);
}

a {
    color: rgba(129, 230, 217);
    color: var(--color-primary);
    transition: all 400rem ease;
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: rgba(129, 230, 217);
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid rgba(129, 230, 217);
    border: 1px solid var(--color-primary);
    transition: all 400rem ease;
    transition: var(--transition);
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: rgba(26, 32, 44);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: rgba(129, 230, 217);
    background: var(--color-primary);
    color: rgba(26, 32, 44);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ================= MEDIA QUERIES (MEDIUM DEVICES) ================== */
@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md)
    }

    section {
        margin-top: 6rem;
    }
}

/* ================= MEDIA QUERIES (SMALL DEVICES) =================== */
@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-sm)
    }

    section > h2 {
        margin-top: 2rem;
    }
}
